@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Lato', sans-serif;
}

.signup-page {
  background: linear-gradient(#1f1a3a, #315264);
}

a {
  text-decoration: none;
}
.nav {
    display: flex;
    padding: 20px 90px;
    background: #131313;
    align-items: center;
    justify-content: space-between;
}

.logo h2 {
    color: #5091aa;
    font-size: 2rem;
}

.links {
    display: flex;
    align-items: center;
}

.favorites {
    margin-right: 30px;
    transition: 0.6s ease;
}


.favorites:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.signin-button {
    padding: 10px;
    background: rgb(245, 245, 245);
    border-radius: 50px;
    display: flex;
    transition: 0.6s ease;
    cursor: pointer;
    color: rgb(17, 17, 17);
    align-items: center;
    justify-content: center;
    width: 80px;
    width: 7rem;
}

.signin-button:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

a {
    text-decoration: none;
}
.menu {
    align-items: center;
}


/*///// QUERIES //////*/




@media screen and (max-width: 600px) {
    .nav {
        font-size: 0.9rem;
        padding: 10px 20px;
    }

    .logo h2 {
        font-size: 1.2rem;
    }

    .favorites {
        margin-right: 10px;
    }

    .heading {
        padding: 0;
        text-align: left;
        font-size: 0.7rem;
    }

    .signin-button {
        padding: 7px;
        width: 90px;
    }

    .menu {
        display: flex;
        font-size: 0.7rem;
    }
}


.landingpage {
    background: #131313;
    height: 100vh;
    color: white;
}

.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 6%;
    padding: 0 5%;
}

.title {
    margin-bottom: 10px;
    font-weight: 900;
    color: #f1f1f1;
    font-size: 2rem;
    width: 60%;
    text-align: center;
}

.description {
    margin-bottom: 30px;
    width: 60%;
    color: #a8a7a7;
    line-height: 2.3rem;
    text-align: center;
}

.button-signup {
    padding: 10px;
    background: #6fbfdf;
    border-radius: 50px;
    color: #1f1f1f;
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    width: 120px;
    transition: 0.5s ease;
}

.button-signup:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background: #a0d8ee;
    color: #0e0d0d;
}


@media screen and (max-width: 1000px) {
    .title {
        font-size: 1.6rem;
        width: 80%;
    }

    .description {
        width: 80%;
    }
}

@media screen and (max-width: 600px) {
    .hero {
        margin-top: 50px;
    }

    .title {
        font-size: 1.2rem;
        width: 85%;
        text-align: left;
    }

    .description {
        width: 80%;
        text-align: left;
        font-size: 0.7rem;
    }
}

@media screen and (max-width: 480px) {
    .title {
        font-size: 1rem;
    }

    .description {
        width: 90%;
        font-size: 0.5rem;
    }

    .button-signup {
        font-size: 0.7rem;
        width: 80px;
    }
}
.signin-page {
    height: 100vh;
    background: #131313;
}

 a {
     color: white;
 }



 
.signup__page {
    height: 100vh;
    background: #131313;
}

 a {
     color: white;
 }

 @media screen and (max-width : 1200px) {
    .nav {
        padding: 15px;
    }
 }
/* Homepage */
.homepage {
    background: #1f1f1f;
}

/* Nav Homepage */

.menu {
    display: flex;
}
 
.discover {
    transition: 0.6s ease;
}

.discover:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


/* Hero Section */ 
.hero-landing {
    background-image: url(/devflix-project/static/media/fast9.813d3aa7.jpeg);
    height: 620px;
    background-size: cover;
    background-position: center;
}

.hero-text {
    position: absolute;
    margin-top: 150px;
    padding-left: 30px;
}

.hero-heading {
    color: white;
    width: 30%;
    font-size: 2.7rem;
}

.hero-heading  h1{
    margin-bottom: 20px;
}

.hero-description {
    width: 40%;
    line-height: 25px;
}

.hero-span {
    font-size: 1.3rem;
    color: rgb(192, 192, 192);
}

.hero-description h3 {
    color: #689fb6;;
    font-size: 1.3rem;
    margin-top: 7px;
}





/* Popular movies */
.heading {
    padding: 0 60px;    
    margin-top: 40px;
    color: rgb(238, 238, 238);
}


ul, 
li {
    list-style: none;
}

.cards {
    display: flex;
    flex-wrap: wrap;
    overflow: auto;
    align-items: center;
    justify-content: center;
}

.movie {
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
    overflow: hidden;
    position: relative;
    width: 300px;
    min-height: 450px;
    align-items: center;
}

.movie img {
    border-radius: 2px;
}

.movie-hover {    
    border-radius: 0 0 2px 2px;
    background-color: #d4d4d4;
    position: absolute;
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    overflow: auto;
    padding: 1.2rem;
    bottom: 0;
    transition: 0.1s ease-in-out;
    left: 0;
    max-height: 100%;
    right: 0;
}

.movie:hover .movie-hover {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
}

.movie-card {
    margin: 5px 10px;
}

.movie-title {
    margin-bottom: 12px;
    font-size: 1.6rem;
}

.movie-info {
    line-height: 1.3rem;
    color: rgb(61, 61, 61);
}

.hearticon {
    padding: 5px;
    width: 100%;
    cursor: pointer;
    border: none;
    margin: 10px 0;
    color: #ffffff;
    box-shadow: 2px rgba(0, 0, 0, 0.5);
    border-radius: 2px;
    display: flex;
    overflow: auto;
    transition: 0.5s ease;
    justify-content: center;
    align-items: center;
}


button {
    transition: 0.4s ease;
}

button:disabled {
  background-color: rgba(50, 87, 110, 0.212);
  cursor: auto;
}

button:disabled h2 {
    color: #d4d4d4;
}



/*//// QUERIES ////*/

@media screen and (max-width: 1250px) {
    .hero-heading {
        width: 35%;
        font-size: 2rem;
    }
    .hero-description {
        width: 60%;
        line-height: 25px;
    }
}

@media screen and (max-width: 650px) {
    .hero-landing {
        height: 460px;
    }

    .hero-heading {
        font-size: 1.3rem;
        width: 100%;
    }

    .hero-span {
        font-size: 1rem;
    }

    .hero-description {
        width: 80%;
        line-height: 25px;
    }
    
    .hero-description h3 {
        font-size: 1rem;
    }

    .heading {
        font-size: 0.9rem;
        padding: 0 10px;
    }
    .w4 {
        width: 5rem;
    }
}

@media screen and (max-width: 480px) {
    .hero-text {
        padding-left: 10px;
    }

    .hero-heading {
        font-size: 1rem;
    }

    .hero-span {
        font-size: 0.9rem;
    }

    .heading {
        font-size: 0.7rem;
    }

    .favorites {
        font-size: 0.5rem;
    }

    .signin-button {
        width: 70px;
        font-size: 0.5rem;
    }

    .movie {
        min-height: 250px;
        max-width: 230px;
        max-height: 330px;
    }
}
